exports.apiErrorController = (err) => {
  if (err.status === 409) {
    return('   e-mail  ')
  } else if (err.status === 401) {
    return(' ')
  } else if (err.status === 500) {
    return('   ,   ')
  } else if (err.status === 400) {
    return('    ')
  } else {
    return('-   ,    ')
  }
}
